import { Component, OnInit } from '@angular/core';
import {HunkRichTextComponent} from  '../../components/hunk-richtexteditor/hunkRichTexteditor.component';
import {APIService} from  '../../globals/services/API.service';

@Component({
  moduleId: module.id,
  selector: 'app-kapcsolat',
  templateUrl: 'kapcsolat.component.html',
  styleUrls: ['kapcsolat.component.css'],
  directives: [HunkRichTextComponent],
  providers:[APIService]
})
export class KapcsolatComponent implements OnInit {
  content:String='';
  cim:String='';
  constructor(private _APIService:APIService) {}

  ngOnInit() {
    this._APIService.getResponseGET('api/kapcsolat/','').subscribe(
                    data=> {
                    this.content=data.tartalom;
                      this.cim=data.cim;

                },
                    error=> {
                    console.log(error);
                })
  }
  contentSaved(data){
    this.content=data;
    this.save();
  }
  cimSaved(data){
  this.cim=data;
  this.save();
}
  save(){
    let params={id:1,tartalom:this.content,cim:this.cim};
    this._APIService.getResponse('api/kapcsolat/',params).subscribe(
        data=> {
        this.content=data.tartalom;
        this.cim=data.cim;

      },
        error=> {
        console.log(error);
      })
  }
}
